body {
    color: #ffffff;
    font-family: "Roboto", sans-serif;
    font-size: 18px;
  }
  
  .wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
  }
  
  .container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 10px;
  }
  
  .header,
  .footer {
    background-color: rgba(0, 103, 163, 1);
    color: #ffffff;
  }
  
  .header {
    font-size: 28px;
  }
  
  .header .container,
  .footer .container {
    height: 55px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  
  .user {
    display: flex;
    align-items: center;
    position: relative;
    z-index: 1;
    cursor: pointer;
  }
  
  .profile {
    display: flex;
    flex-direction: column;
    gap: 3px;
    font-size: 14px;
    background-color: #ffffff;
    color: #000000;
    border: 1px solid #ebecf0;
    border-radius: 5px;
    width: 134px;
    padding: 5px 2px;
    position: absolute;
    top: 50px;
    right: 0;
  }
  
  .profile::after {
    content: "";
    width: 11px;
    height: 11px;
    background-color: #ffffff;
    position: absolute;
    transform: rotate(45deg);
    top: -5px;
    right: 38px;
  }
  
  .profile p {
    padding: 5px 3px;
    border-radius: 3px;
  }
  
  .profile p:hover {
    background-color: #ebecf0;
    cursor: pointer;
  }
  
  .main {
    background-color: rgba(0, 121, 191, 1);
    ;
    flex-grow: 1;
    padding: 15px 0;
  }
  
  .main .container {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
    gap: 15px;
  }
  
  .block {
    padding: 8px 5px;
    background-color: #616060;
    border-radius: 10px;
    width: 282px;
  }
  
  .block h3 {
    margin-bottom: 10px;
  }
  
  .block input {
    margin-bottom: 10px;
    width: 100%;
  }
  
  .block-list {
    display: flex;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 10px;
    overflow: auto;
    max-height: 69vh;
  }
  
  .block-task {
    border-radius: 5px;
    background-color: #a5a5a5;
    display: flex;
    padding-right: 5px;
    overflow: hidden;
  }
  
  .block-task button {
    transform: rotate(45deg);
    transition: all 200ms linear;
  }
  
  .block-task button:hover {
    transform: scale(1.2) rotate(45deg);
    cursor: pointer;
  }
  
  .block-task-none {
    padding: 8px;
  }
  
  .block-task a {
    display: inline-block;
    padding: 8px;
    width: 100%;
    text-align: start;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  
  .block-arrow {
    border-radius: 5px;
    margin-bottom: 10px;
    background-color: #fff;
    position: relative;
  }
  
  .block-arrow span {
    display: flex;
    padding: 10px 8px;
    justify-content: flex-end;
  }
  
  .select__control {
    width: 100% !important;
    min-height: 34px !important;
    border-radius: 5px !important;
    border: none !important;
    font-size: 18px !important;
    margin-bottom: 10px !important;
    background: #fff url(./images/taskArrow.svg) no-repeat 96% !important;
    box-shadow: none !important;
  }
  
  .select__control:hover {
    cursor: pointer;
  }
  
  .select__control--is-focused {
    border-color: #fff !important;
    border-width: 0 !important;
  }
  
  .select__control--menu-is-open {
    border-color: #fff !important;
    border-width: 0 !important;
  }
  
  .select__placeholder {
    display: none !important;
  }
  
  .select__menu {
    margin-top: 4px !important;
    box-shadow: none !important;
  }
  
  .select__menu-list {
    padding: 0 !important;
    border-radius: 5px !important;
  }
  
  .select__option {
    cursor: pointer !important;
    background-color: #fff !important;
    color: #000000 !important;
  }
  
  .select__option:hover {
    background-color: #dedede !important;
  }
  
  .button {
    display: flex;
    justify-content: center;
    gap: 5px;
    color: #d6d6d6;
    padding: 3px 5px;
    border-radius: 5px;
  }
  
  .button:hover {
    background-color: #d6d6d6;
    opacity: 0.9;
  }
  
  .button:disabled {
    cursor: default;
    opacity: 0.6;
    background: none;
  }
  
  .description__container {
    border-radius: 5px;
    background-color: #ffffff;
    width: 100%;
    padding: 25px;
    height: 80vh;
    position: relative;
  }
  
  .description__title {
    font-size: 28px;
    font-weight: 700;
    margin-bottom: 30px;
    width: calc(100% - 25px);
    overflow: hidden;
    text-overflow: ellipsis;
    color: #000000;
  }
  
  .description__text {
    width: 100%;
    height: 90%;
    resize: none;
    background: none;
  }
  
  .description__btn {
    position: absolute;
    top: 20px;
    right: 25px;
    transition: all 100ms linear;
  }
  
  .description__btn:hover {
    transform: scale(1.1);
    cursor: pointer;
  }
  
  .footer-left {
    display: flex;
    gap: 30px;
  }
  
  @media (max-width: 600px) {
    .logo {
      font-size: 24px;
    }
  
    .main .container {
      justify-content: center;
    }
  
    .footer .container {
      flex-wrap: wrap;
    }
  }
  
  @media (max-width: 400px) {
    .header .container {
      justify-content: flex-end;
    }
  
    .logo {
      display: none;
    }
  
    .footer-left {
      gap: 10px;
    }
  }
  